.main {
    display: flex;
    align-items: center;
    height: 100vh;
    

    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: flex-end;
        justify-content: center;
        z-index: 2;

        @include viewport--md {
           display: none;
        }
    }

    &__inner {
        width: 100%;
        display: flex;
        justify-content: center;
    }


    &__title {
       display: flex;
       align-items: flex-end;
   }

    h1 {
        margin: 0 45px 0 0;
        max-width: 562px;
        font-size: 96px;
        line-height: 96px;
        color: $color-neon-blue;
        font-weight: 400;
        
    }

    &__content {

        p {
            max-width: 352px;
            font-size: 30px;
            line-height: 40px;

            @include viewport--md {
                color: $color-default-white;
            }
        }

        @include viewport--md {
            position: absolute;
            top: 0;
            left: -100%;
            width: 100%;
            height: 100vh;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            background-color: $color-neon-blue;
            z-index: 4;
            transition: 1s;

            &.active {
                left: 0;
                transition: 1s;
            }
        }
    }

    &__img {
        margin: 0 0 35px 0;
    }
}

.animate {
    animation: fade 5s;
}
